*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family:"Poppins", sans-serif;
}
.header {
	width: 100%;
	height: 100vh;
	background: url("assets/saltodelangel.jpg");
	background-repeat: no-repeat;
	background-size: cover;
	overflow: hidden;
}
.navbar {
	top: 0;
	width: 100%;
	height: 100px;
	position: fixed;
	background: #1f2235;
	z-index: 999;
}
.navbar_container {
	display: flex;
	height: 100px;
	align-items: center;
	justify-content: space-between;
}
.navbar_left-logo img{
	width: 70px;
	border-radius: 50%;
}
.navbar_right{
	display: flex;
	list-style-type: none;
}
.navbar_right li a {
	text-decoration: none;
	color: #fff;
	font-size: 18px;
	font-weight: 500;
	padding: 0 15px;
}
.container {
	width: 1200px;
	margin: 0 auto;
}
.bgMain{
	background: #1f2235;
}
.justifyConter{
	justify-content: center;
}

.row {
	display: flex;
	width: 100%;
	flex-wrap: wrap;
}
.col-4 {
	width: 33.3333333333%;
}
.col-6 {
	width: 50%;
}
.header_content {
	display: flex;
	align-items: center;
	height: 100vh;
	width: 100%;
	color: #fff ;
}
.header_ul{
	list-style-type: none;
	display: flex;
}
.header_ul li {
	width: 50px;
	background: #111;
	height: 50px;
	border: 2px solid #ff4a57;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 0 10px;
}
.header_ul li:first-child {
	margin-left:  0;
}
.header_section h1 {
	font-size: 57px;
	font-weight: 700;
	margin-bottom: 20px;
}
.header_section p {
	font-size: 26px;
	font-weight: 300;
	line-height: 40px;
	margin-bottom: 50px;
}

.btn {
	outline: none;
	border: none;
	color: #fff;
	padding: 10px 10px;
	text-decoration: none;
}
.btn-outline {
	border: 2px solid #ff4a57;
	border-radius: 50px;
	padding: 13px 40px;
}
.btn-smart {
	background: #ff4a57;
	padding: 13px 25px;
	border-radius: 50px;
}
.play {
	font-size: 25px !important;
	padding-top: 10px;
}
.banner_img {
	padding: 0 30px;
	margin-top: 100px;
}
/* Services */
.services {
	background: #1f2235;
	padding: 100px 0;
	color: #fff;
}
.common {
	width: 33.3333333333%;
	margin: 20px auto;
	text-align: center;
}
.heading {
	margin-bottom: 20px;
	font-weight: 500;
	color: #ff4a57;
	font-size: 16px;
}
.mainHeader {
	font-size: 32px;
	margin-bottom: 20px;
	color: #fff;
}
.mainContent {
	font-size: 16px;
	color: #dfdfdf;
}
.commonBorder {
	background-color: #ff4a57;
	height: 3px;
	width: 120px;
	margin: 30px auto;
}
.services_box {
	padding: 40px;
	background: #23263a;
	color: #fff;
	margin: 20px;
	transition: all 0.5s linear;
}
.services_box:hover {
	background: #ff4a57;
}
.commonIcons {
	font-size: 40px;
	color: #ff4a57;
	margin-bottom: 15px;
}
.services_box:hover .commonIcons{
	color: #fff;
}
.services_box-header {
	font-size: 20px;
	font-weight: 500;
	margin-bottom: 15px;
}
.services_box-p {
	font-size: 16px;
	color: #dfdfdf;
}
.about {
	width: 100%;
	background: #23263a;
	padding: 100px 0;
}
.about_img {
	padding: 30px;
}
.about_img img {
	width: 300px;
	height: 400px;
}
.h-650 {
	height: 650px;
}
.alignCenter{
	align-items: center;
}
.about_info{
	color:#fff;
}
.about_info h1{
	margin-bottom: 30px;
}
.about_info-p1 {
	font-size: 14px;
	margin-bottom: 20px;
}
.about_info-p2 {
	font-size: 14px;
	margin-bottom: 20px;
}
.info_contacts{
	margin-top: 20px;
}
.info_contacts strong {
	color: #ff4a57;
}
.info_contacts p{
	margin-top: 3px;
}
.info_contacts .col-6{
	margin: 10px 0;
}
.prices {
	width: 100%;
	padding: 100px 0;
	background:#1f2235;
}
.price {
	background: #23263a ;
	padding: 25px;
	border-radius: 8px;
	margin: 35px 20px;
	color: #fff;
	text-align: center;
}
.priceHeading {
	font-size: 20px;
	font-weight: 500;
	margin-bottom: 15px;
}
.price_rs{
	font-size: 50px;
	font-weight: 200;
	margin-bottom: 15px;
}
.price_rs span{
	vertical-align: middle;
	font-size: 25px;
}
.price ul{
	list-style: none;
}
.col-4{
	margin: 10px 0;
	width: 25%;
}
.price ul li{
	margin: 5px 0;
	border-bottom: 1px solid #3c3c3c;
	font-size: 14px;
	margin: 15px 0;
}
.price_btn {
	margin-top: 40px;
}
.contact {
	padding: 100px 0;
	background: #23263a;
}
.contactSection-logo{
	margin-bottom: 20px;
	text-align: center;
}
.contactSection-logo img {
	margin: o auto;
	width: 130px;
}
.contactSection p {
	display: flex;
	justify-content: center;
	color: #fff;
	font-size: 14px;
	margin-bottom: 20px;
}
.contactCircles {
	width: 100%;
	display: flex;
	list-style: none;
	justify-content: center;
}
.contactCircles li {
	display: flex;
	width: 50px;
	height: 50px;
	border-radius: 50%;
	align-items: center;
	justify-content: center;
	color: #fff;
	background: #1f2235 ;
	margin: 0 10px;
}
.toggle{
	position: fixed;
	top: 20px;
	right: 25px;
	background: #000;
	color: #fff;
	width: 50px;
	height: 50px;
	border-radius: 50%;
	cursor: pointer;
	align-items: center;
	justify-content: center;
	display: none;
}
@media(max-width: 770px){
	.container{
		width: 100%;
		padding: 0 50px;
	}
	.col-6{
		width: 100%;
	}
	.col-3{
		width: 100%;
	}
	.col-4{
		width: 100%;
	}
	.navbar_right{
		flex-direction: column;
		width: 100%;
		align-items: center;
		background: #1f2235;
		position:fixed;
		top: 0;
		left: 0;
	}
	.navbar_right li a{
		display: block;
		padding: 20px 0;
		font-size: 20px;
	}
	.toggle{
		display: flex;
	}
	.banner_img img{
		display: none;
	}
	.header_section h1 {
		font-size: 35px;
		font-weight: 700;
		margin-bottom: 20px;
	}
	.header_section p {
		font-size: 20px;
		font-weight: 300;
		line-height: 30px;
		margin-bottom: 50px;
	}
	.common {
		width: 100%;
	}
	.h-650{
		height: auto;
	}
	.about_img {
		width: 100%;
		text-align: center;
		padding: 0px;
	}
}